















import Vue from 'vue'

export default Vue.extend({
	name: 'Card',

	components: {
	},

	props: {
		title: {
			type: String,
			default: '',
			required: false,
		},

		order: {
			type: Number,
			default: 0,
			required: true,
		},

		current: {
			type: Number,
			default: -1,
			required: true,
		},

		cover: {
			type: String,
			default: '',
			required: false,
		},
	},

	data: () => ({
		active: false,
	}),

	computed: {
		activeClass() {
			return this.current === this.order
				? 'active'
				: 'stacked'
		},

		cardN() {
			const isActive = this.order === this.current //(this.active as boolean)
			const offset: number = isActive ? 0 : 18

			switch (true) {
				case this.current < 0: {
					return {
						top: `${(this.order as number) * offset}vmin`,
						marginTop: 0,
					}
				}
				case this.order > this.current: {
					return {
						top: `100%`,
						marginTop: `4vmin`,
					}
				}
				case this.order == this.current: {
					return {
						top: `0`,
						marginTop: 0,
					}
				}
			}
		}
	},

	methods: {
		toggleActive() {
			this.active = !this.active
			this.$emit('update', this.order)
		}
	}
})
