
















import Vue from 'vue'
import { mapState } from 'vuex'
import { shit } from '@/utils/Rand'
import { biology } from '@/utils/Player'

export default Vue.extend({
	name: 'Brief',

	components: {
	},

	props: {
		id: {
			type: String,
			default: '',
			required: false,
		}
	},

	computed: {
		...mapState('sess', {
			'people': 'people',
			'player': 'id'
		}),

		vId() { return +this.id + 1 },

		isPlayer() { return this.id === this.player },
		isRight() {
			const next = (+this.player - 1) % this.people.length
			return next.toString() === this.id
		},
		isLeft() {
			const next = (+this.player + 1) % this.people.length
			return next.toString() === this.id
		},

		vInfo() {
			const current = this.people[this.id]
			return [
				biology(current),
				current.proffession,
				current.health,
				current.hobby,
				current.trait,
				current.phobia,
				current.perk,
				current.baggage,
			].filter(shit).join('; ')
		}
	},
})
